import React, { FC, useEffect, useRef } from "react";
import classNames from "classnames/bind";

import { convertQueryStringToQueryObject } from "common/utils/queryString";
import {
    PAGES_HAS_TRADEDOUBLER_PIXEL,
    setValueOfCookieTduid,
} from "common/services/TradedoublerPixelService";
import { Details } from "common/components/Details";
import { Container } from "common/components/Container";
import { Questions } from "common/components/new/Questions";
import { PriceBlock } from "common/components/new/PriceBlock";
import { NewAppointment } from "common/components/NewAppointment";
import { DetailsProvider } from "common/components/Details/hooks/context";
import { MaxWidthContainer } from "common/components/new/MaxWidthContainer";
import { FeaturesWithImages } from "common/components/new/FeaturesWithImages";
import { ExperimentService } from "common/services/ExperimentService";
import { useComputedFixedButtonOffset } from "common/hooks/useComputedFixedButtonOffset";
import { useFixedButtonContext } from "common/context/fixedButtonContext";
import { Heading } from "common/components/new/Heading";
import { CountryVersion } from "common/constants/enum";

import { Head } from "components/Head";
import { Footer } from "components/Footer";
import { Advantages } from "components/Advantages";
import { PatientFeedbackMediaQueryController } from "components/PatientFeedbackMediaQueryController";

import { NewMainPageLayoutProps } from "./NewMainPageLayout.types";
import styles from "./NewMainPageLayout.module.scss";

const cx = classNames.bind(styles);

export const NewMainPageLayout: FC<NewMainPageLayoutProps> = ({
    content,
    version,
    language,
}) => {
    const {
        head,
        prices,
        details,
        contacts,
        questions,
        advantages,
        isMailPage,
        appointment,
        patientFeedbacks,
        futureOfOrthodontics,
    } = content;
    const hasWindow = typeof window !== "undefined";

    const appointmentRef = useRef<HTMLDivElement>(null);

    const {
        isFixedButtonActive,
        scrollToAppointment,
    } = useComputedFixedButtonOffset(appointmentRef);

    const { setFixedButton } = useFixedButtonContext();

    useEffect(() => {
        setFixedButton(isFixedButtonActive);
    }, [isFixedButtonActive]);

    useEffect(() => {
        if (!hasWindow) {
            return;
        }

        const { pathname, search } = location;
        const queryParams = convertQueryStringToQueryObject(search);
        const { tduid } = queryParams;

        if (PAGES_HAS_TRADEDOUBLER_PIXEL.includes(pathname) && tduid) {
            setValueOfCookieTduid(tduid);
        }
    }, [hasWindow]);

    return (
        <MaxWidthContainer className={cx("root")}>
            <ExperimentService />

            <Head
                {...head}
                contacts={contacts}
                button={{ ...head.button, onClick: scrollToAppointment }}
                isMailPage={isMailPage}
                isFixedButtonActive={isFixedButtonActive}
                className={cx("head")}
            />
            <div ref={appointmentRef}>
                <Container>
                    <Heading
                        className={cx("appointment-title", {
                            "appointment-title_ss-pro":
                                version === CountryVersion.GERMANY,
                            "appointment-title_germany":
                                version === CountryVersion.GERMANY,
                        })}
                    >
                        {appointment.title}
                    </Heading>
                    <NewAppointment
                        version={version}
                        language={language}
                        {...appointment}
                        className={cx("container")}
                    />
                </Container>
            </div>
            {futureOfOrthodontics && (
                <FeaturesWithImages
                    className={cx("container")}
                    {...futureOfOrthodontics}
                />
            )}
            <Container>
                {prices && (
                    <PriceBlock
                        {...prices}
                        version={version}
                        language={language}
                        className={cx("container", "prices")}
                        onButtonClick={scrollToAppointment}
                    />
                )}
                <Advantages {...advantages} className={cx("advantages")} />
            </Container>
            <Container className={cx("container-delails")}>
                <DetailsProvider>
                    <Details
                        {...details}
                        noSlider={true}
                        className={cx("container")}
                        onButtonClick={scrollToAppointment}
                    />
                </DetailsProvider>
            </Container>
            <Container>
                <PatientFeedbackMediaQueryController
                    className={cx("container")}
                    {...patientFeedbacks}
                />
                <Questions {...questions} className={cx("container")} />
                {contacts && <Footer {...contacts} isMailPage={isMailPage} />}
            </Container>
        </MaxWidthContainer>
    );
};
