import React, { FunctionComponent, useContext } from "react";
import classNames from "classnames/bind";

import { getAnalyticsId } from "common/utils/AnalyticsId";
import { Button, ButtonTheme, ButtonSize } from "common/components/new/Button";
import { Icon } from "common/components/Icons";
import ArrowRightIcon from "common/components/Icons/icons/24/arrow-right.svg";
import ArrowLeftIcon from "common/components/Icons/icons/24/arrow-left.svg";

import { DetailsTypes } from "../../hooks/reducers";
import { DetailsContext } from "../../hooks/context";

import { ActionsDetailsContentProps } from "./Actions.types";
import styles from "./Actions.module.scss";

const cx = classNames.bind(styles);

export const Actions: FunctionComponent<ActionsDetailsContentProps> = ({
    actionShow,
    actionHow,
    actionBooking,
    className,
    onButtonClick,
}) => {
    const { dispatch } = useContext(DetailsContext);

    const handlePrevStep = () => {
        dispatch({
            type: DetailsTypes.SetPrevActiveStage,
        });
    };

    const handleNextStep = () => {
        dispatch({
            type: DetailsTypes.SetNextActiveStage,
        });
    };

    return (
        <div className={cx("root", className)}>
            <div className={cx("actions-info")}>
                {actionBooking && (
                    <Button
                        id={getAnalyticsId({
                            zone: "actions",
                            label: "booking",
                        })}
                        className={cx("action-consult")}
                        size={ButtonSize.M}
                        onClick={onButtonClick}
                        {...actionBooking}
                    />
                )}

                {actionShow && (
                    <Button
                        id={getAnalyticsId({
                            zone: "actions",
                            label: "clinics",
                        })}
                        className={cx("action-consult")}
                        size={ButtonSize.M}
                        {...actionShow}
                    />
                )}

                {actionHow && (
                    <Button
                        id={getAnalyticsId({
                            zone: "actions",
                            label: "read-more",
                        })}
                        className={cx("action-read-more")}
                        theme={ButtonTheme.BORDERED_TRANSPARENT}
                        size={ButtonSize.M}
                        {...actionHow}
                    />
                )}
            </div>

            <div className={cx("actions-controls")}>
                <button
                    className={cx("action-navigate")}
                    onClick={handlePrevStep}
                >
                    <Icon>
                        <ArrowLeftIcon />
                    </Icon>
                </button>

                <button
                    className={cx("action-navigate")}
                    onClick={handleNextStep}
                >
                    <Icon>
                        <ArrowRightIcon />
                    </Icon>
                </button>
            </div>
        </div>
    );
};
