import React, { FunctionComponent, useContext } from "react";
import classNames from "classnames/bind";

import { Text } from "common/components/Text";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";

import { DetailsContext } from "../../hooks/context";
import { DetailsTypes } from "../../hooks/reducers";
import { TIME_SHOW_STAGE_IN_SEC } from "../../constants";
import { Label } from "../Label";

import { StageDetailsContentProps } from "./Stage.types";
import styles from "./Stage.module.scss";

const cx = classNames.bind(styles);

export const Stage: FunctionComponent<StageDetailsContentProps> = ({
    text,
    isActive,
    index,
    title,
    textLabel,
}) => {
    const { dispatch } = useContext(DetailsContext);

    const handleClickOnStage = () => {
        dispatch({
            type: DetailsTypes.SetActiveStage,
            payload: index,
        });
    };

    return (
        <div className={cx("root")} onClick={handleClickOnStage}>
            <div className={cx("meter")}>
                <span
                    className={cx("progress-container", {
                        "progress-container_is-active": isActive,
                    })}
                >
                    <span
                        className={cx("progress")}
                        style={{
                            animationDuration: `${TIME_SHOW_STAGE_IN_SEC}s`,
                        }}
                    />
                </span>
            </div>

            <div
                className={cx("description", {
                    "description_is-active": isActive,
                })}
            >
                {title && (
                    <Heading
                        className={cx("title")}
                        type={HeadingType.S}
                        tag={HeadingTag.H3}
                    >
                        {title}
                    </Heading>
                )}
                <Text
                    className={cx("text", {
                        "text_with-label": textLabel,
                    })}
                >
                    {text}
                </Text>
                {textLabel && (
                    <Label
                        className={cx("label", {
                            "label_is-active": isActive,
                        })}
                    >
                        {textLabel}
                    </Label>
                )}
            </div>
        </div>
    );
};
