import React, { FunctionComponent } from "react";
import classNames from "classnames/bind";

import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Text } from "common/components/Text";
import { Picture } from "common/components/Picture";

import { Label } from "../Label";
import { StageImageElementProps } from "../StageImageElement";
import styles from "./StageImageElement.module.scss";

const cx = classNames.bind(styles);

export const StageImageElement: FunctionComponent<StageImageElementProps> = ({
    image,
    count,
    text,
    title,
    isActive,
    noSlider,
    textLabel,
}) => {
    return (
        <div
            className={cx("root", {
                "root_margin-bottom": noSlider,
            })}
        >
            <div
                className={cx("image-content", {
                    "image-content_full-width": noSlider,
                })}
            >
                <Picture
                    classNameImg={cx("image", {
                        "image_is-active": isActive,
                    })}
                    {...image}
                />
            </div>
            <div
                className={cx("content", {
                    content_flex: noSlider,
                })}
            >
                {noSlider && <span className={cx("count")}>{count}</span>}
                <div>
                    {title && (
                        <Heading
                            className={cx("title")}
                            type={HeadingType.M}
                            tag={HeadingTag.H3}
                        >
                            {title}
                        </Heading>
                    )}

                    <Text
                        className={cx("description", {
                            "description_with-label": textLabel,
                        })}
                    >
                        {text}
                    </Text>
                    {textLabel && (
                        <Label className={cx("label")}>{textLabel}</Label>
                    )}
                </div>
            </div>
        </div>
    );
};
