import { FC } from "react";
import classNames from "classnames/bind";

import { StageImageElement } from "../StageImageElement";
import { StagesDetailsContentProps } from "../Stages";

import styles from "./StagesImages.module.scss";

const cx = classNames.bind(styles);

export const StagesImages: FC<StagesDetailsContentProps> = ({
    stages,
    noSlider,
}) => {
    return (
        <div
            className={cx("root", {
                ["root_no-margin-left"]: noSlider,
            })}
        >
            {stages.map((stage, index) => (
                <StageImageElement
                    key={index}
                    count={index + 1}
                    noSlider={noSlider}
                    {...stage}
                />
            ))}
        </div>
    );
};
