import { FC } from "react";
import classNames from "classnames/bind";

import { Container } from "common/components/Container";
import { Heading, HeadingTag } from "common/components/new/Heading";
import { Text } from "common/components/Text";
import { Icon } from "common/components/Icons";
import DoneIcon from "common/components/Icons/icons/24/done.svg";
import { Picture } from "common/components/Picture";

import { FeaturesWithImagesProps } from "./FeaturesWithImages.types";
import styles from "./FeaturesWithImages.module.scss";

const cx = classNames.bind(styles);

export const FeaturesWithImages: FC<FeaturesWithImagesProps> = ({
    title,
    description,
    features,
    indexMobilePicture,
    className,
}) => {
    return (
        <section className={cx("root", className)}>
            <Container>
                <Heading tag={HeadingTag.H2} className={cx("title")}>
                    {title}
                </Heading>
                <Text className={cx("description")}>{description}</Text>
                <ul className={cx("list")}>
                    {features.map(({ image, text }) => (
                        <li key={text} className={cx("item")}>
                            <Icon className={cx("item__icon")}>
                                <DoneIcon />
                            </Icon>
                            <Picture
                                classNameImg={cx("image")}
                                className={cx("item__image")}
                                {...image}
                            />
                            <Text className={cx("item__text")}>{text}</Text>
                        </li>
                    ))}
                </ul>
            </Container>
            <div className={cx("container-image")}>
                <Picture {...features[indexMobilePicture].image} />
            </div>
        </section>
    );
};
