import { FC } from "react";
import classNames from "classnames/bind";

import { HorizontalScroll } from "common/components/HorizontalScroll";

import { StagesImages } from "../StagesImages";
import { StagesDetailsContentProps } from "../Stages";

import styles from "./StagesImagesSliderMobile.module.scss";

const cx = classNames.bind(styles);

export const StagesImagesSliderMobile: FC<StagesDetailsContentProps> = ({
    stages,
    noSlider,
}) => {
    return (
        <div className={cx("root")}>
            {noSlider ? (
                <StagesImages stages={stages} noSlider={noSlider} />
            ) : (
                <HorizontalScroll>
                    <StagesImages stages={stages} />
                </HorizontalScroll>
            )}
        </div>
    );
};
