import React, { FunctionComponent, useContext, useEffect } from "react";
import classNames from "classnames/bind";

import { Text } from "common/components/Text";
import { Heading, HeadingTag } from "common/components/new/Heading";

import { DetailsContext } from "./hooks/context";
import { DetailsTypes } from "./hooks/reducers";
import { TIME_SHOW_STAGE_IN_SEC } from "./constants";
import { StagesImagesSliderMobile } from "./components/StagesImagesSliderMobile";
import { Actions } from "./components/Actions";
import { Stages } from "./components/Stages";
import { StagesImages } from "./components/StagesImages";

import { DetailsContentProps } from "./Details.types";
import styles from "./Details.module.scss";

const cx = classNames.bind(styles);
const timeShowStageInMs = TIME_SHOW_STAGE_IN_SEC * 1000;

export const Details: FunctionComponent<DetailsContentProps> = (props) => {
    const {
        title,
        description,
        stages,
        actionShow,
        actionHow,
        actionBooking,
        className,
        onButtonClick,
        noSlider,
    } = props;

    const { state, dispatch } = useContext(DetailsContext);

    useEffect(() => {
        const setDataReducer = () => {
            dispatch({
                type: DetailsTypes.SetStages,
                payload: stages,
            });
        };

        setDataReducer();
    }, [stages]);

    useEffect(() => {
        const interval = setInterval(() => {
            dispatch({
                type: DetailsTypes.SetNextActiveStage,
            });
        }, timeShowStageInMs);
        return () => clearInterval(interval);
    }, [state]);

    return (
        <div className={cx("root", className)}>
            <div className={cx("column", "column-content")}>
                <div className={cx("content")}>
                    <div
                        className={cx("info-stages", {
                            ["info-stages_no-padding"]: noSlider,
                        })}
                    >
                        <Heading tag={HeadingTag.H2} className={cx("title")}>
                            {title}
                        </Heading>

                        {description && (
                            <Text className={cx("description")}>
                                {description}
                            </Text>
                        )}

                        <Stages stages={state.stages} />
                    </div>

                    <StagesImagesSliderMobile
                        noSlider={noSlider}
                        stages={state.stages}
                    />
                    <Actions
                        actionShow={actionShow}
                        actionHow={actionHow}
                        actionBooking={actionBooking}
                        onButtonClick={onButtonClick}
                        className={cx("actions-desktop")}
                    />
                </div>
            </div>
            <div className={cx("column", "column-images")}>
                <div className={cx("stages-images-desktop")}>
                    <StagesImages stages={state.stages} />
                </div>
                <Actions
                    actionHow={actionHow}
                    actionShow={actionShow}
                    onButtonClick={onButtonClick}
                    className={cx("actions-mobile")}
                />
            </div>
        </div>
    );
};
