import React, { createContext, useReducer, Dispatch } from "react";

import { StageDetailsProps } from "common/components/Details/components/Stage";

import { stageReducer, StageActions } from "./reducers";

interface InitialStateType {
    stages: StageDetailsProps[];
}

const initialState = {
    stages: [],
};

const DetailsContext = createContext<{
    state: InitialStateType;
    dispatch: Dispatch<StageActions>;
}>({
    state: initialState,
    dispatch: () => null,
});

const mainReducer = ({ stages }: InitialStateType, action: StageActions) => ({
    stages: stageReducer(stages, action),
});

const DetailsProvider: React.FC = ({ children }) => {
    const [state, dispatch] = useReducer(mainReducer, initialState);

    return (
        <DetailsContext.Provider value={{ state, dispatch }}>
            {children}
        </DetailsContext.Provider>
    );
};

export { DetailsContext, DetailsProvider };
